import Vue from 'vue'

export default {
    name: 'kendo-chat-toolbar-button',
    render () {
        return Vue.prototype._e
    },
    props:{
        name: String,
        text: String,
        iconClass: String,
        attr: Object
    },
    watch: {
        $attrs () {
            this.mergeAttrs()
        }
    },
    mounted () {
        this.mergeAttrs()
    },
    methods: {
        mergeAttrs () {
            if(this.$options.propsData.attr){
                this.attr = Object.assign(this.attr, this.$attrs)
            } else {
                this.$options.propsData.attr = Object.assign({}, this.attr, this.$attrs)
            }
        }
    }
}



